@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: #24252a;
  font-weight: 500;
  font-size: 16px;
  color: #edf0f1;
}

button {
  padding: 9px 25px;
  background-color: rgba(0, 136, 169, 1);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

button:hover {
  background-color: rgba(0, 136, 169, 0.8);
}

.headBackground {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 30px 7%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.logo {
  cursor: pointer;
  font-size: 40px;
}

.array-bar {
  width: 4px;
  display: inline-block;
  margin: 0 3px;
  background-color: turquoise;
}

.array-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
}

.dashboard {
  background-color: rgb(7, 79, 97);
  -webkit-align-content: center;
          align-content: center;
  padding: 20px 7%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  border-radius: 20px;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 50px;
}

.selectAlg {
  display: -webkit-flex;
  display: flex;
}

.buttonNav {
  margin-top: 15px;
  margin-left: 40px;
}

.buttonNav button {
  margin: 5px 10px;
  border: 2px solid white;
}

.subButton {
  border: 2px solid #24252a;
}

.pavement {
  width: 2px;
  display: inline-block;
  margin: 9px 10px;
  background-color: black;
}

.size {
  margin-top: 15px;
}
.speed {
  margin-left: 10px;
  margin-top: 15px;
}

